import { Token } from '@cetus/types'
import { Icon, SingleCoinImage } from '@cetus/ui-kit'
import { IconProps } from '@cetus/ui-kit/src/components/Icon'
import { addComma, d, textEllipses } from '@cetus/utils'
import { Center, Divider, HStack, Text, VStack } from '@chakra-ui/react'
import { useMemo } from 'react'

export interface TradeConfirmAmountInfoProps {
  coinA?: AmountInfoProps
  coinB?: AmountInfoProps
  iconParams?: IconProps
  bg?: string
}
/**
 * 交易确认 展示 amount 信息弹窗
 * @param param0
 * @returns
 */
function TradeConfirmAmountInfo({ coinA, coinB, iconParams, bg = 'bg_secondary' }: TradeConfirmAmountInfoProps) {
  const showCoinA = useMemo(() => {
    if (coinA) {
      let amount = coinA?.amount
      if (amount?.includes(',')) {
        amount = amount.replace(/,/g, '')
      }
      return d(amount).gt(0)
    }
    return false
  }, [coinA])

  const showCoinB = useMemo(() => {
    if (coinB) {
      let amount = coinB?.amount
      if (amount?.includes(',')) {
        amount = amount.replace(/,/g, '')
      }
      return d(amount).gt(0)
    }
    return false
  }, [coinB])

  return (
    <VStack w="100%" bg={bg} borderRadius="12px" border="1px solid" borderColor="border" position="relative" gap="0">
      {showCoinA && (
        <VStack w="100%" h="80px" pl="16px" pr="16px" justifyContent="center">
          <AmountInfo {...coinA!} />
        </VStack>
      )}
      {showCoinA && showCoinB && <Divider borderColor="border" />}

      {showCoinB && (
        <VStack w="100%" h="80px" pl="16px" pr="16px" justifyContent="center">
          <AmountInfo {...coinB!} />
        </VStack>
      )}

      {showCoinA && showCoinB && iconParams && (
        <Center
          w="32px"
          h="32px"
          borderRadius="50%"
          position="absolute"
          top="calc(50% - 18px)"
          right="16px"
          border="1px solid"
          borderColor="border"
          boxShadow="trade_icon_shadow"
          bg={bg}
        >
          <Icon {...iconParams} cursor="unset" />
        </Center>
      )}
    </VStack>
  )
}

type AmountInfoProps = Token & {
  amount: string
}

function AmountInfo({ amount, logo_url, symbol, coin_type }: AmountInfoProps) {
  return (
    <HStack w="100%" justify="space-between">
      <HStack maxW="calc(100% - 40px)">
        <HStack
          maxW="100%"
          overflowY="hidden"
          overflowX="auto"
          sx={{
            '::-webkit-scrollbar': {
              display: 'none'
            }
          }}
        >
          <Text fontSize="24px" color="text_caption" whiteSpace="nowrap" textAlign="left">
            {addComma(amount)}
          </Text>
        </HStack>
        <Text whiteSpace="nowrap" fontSize="16px">
          {textEllipses(symbol, 10)}
        </Text>
      </HStack>
      <HStack gap="8px" justifyContent="end">
        <SingleCoinImage imageUrl={logo_url} imageStyle={{}} coinType={coin_type} />
      </HStack>
    </HStack>
  )
}

export default TradeConfirmAmountInfo
